.container{
    text-align: center;
}
.title{
    margin-top: 70px;
    margin-bottom: 70px;
    color: #FFF;
    font-size: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.title img{
    margin-top: 10px;
    height: 30px;
    width: 30px;
}

.title span{
    color: #5FF;
}

.reset{
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
    background: #1F3540;
    color: #5FF;
    padding: 10px 15px;
    font-size: 18px;
    font-weight: bold;
    margin-top: 50px;
}

.row1,.row2,.row3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.boxes{
    border-collapse: collapse;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
    border-radius: 5px;
    border: 4px solid #5FF;
    cursor: pointer;
    padding: 25px;
}
